<template>
  <div class="container">
    <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
      <FormItem prop="account">
        <Input type="text" v-model="formInline.title" placeholder="">
        <Icon type="ios-book-outline" slot="prepend"></Icon>
        </Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formInline')"></Button>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="modal1 = true"></Button>
      </FormItem>
    </Form>
    <Table border :columns="columns7" :data="data6"></Table>
    <Page :total="total" :page-size="10" @on-change="changePage"></Page>

    <Modal
      v-model="modal1"
      title=""
      width="800"
      scrollable="true"
      ok-text=""
      @on-ok="ok('formItem2')"
    >
      <Form ref="formItem2" :model="formItem2" :rules="ruleItem2" :label-width="80">
        <FormItem label="" prop="title">
          <Input v-model="formItem2.title" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="author">
          <Input v-model="formItem2.author" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="publisher">
          <Input v-model="formItem2.publisher" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="publishtime">
          <DatePicker v-model="formItem2.publishtime" prop="publishtime" type="date" placeholder="" style="width: 200px"></DatePicker>
        </FormItem>
        <FormItem label="" prop="descri">
          <!--<Input v-model="formItem2.descri" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="..."></Input>-->
          <quill-editor v-model="formItem2.descri" ref="VueQuillEditor"
                        :content="content"
                        @change="onEditorChange($event)">
          </quill-editor>
        </FormItem>
      </Form>
    </Modal>

    <!---->
    <Modal
      v-model="modal2"
      title=""
      ok-text=""
      @on-ok="ok2('formItem3')"
    >
      <Form ref="formItem3" :model="formItem3" :rules="ruleItem3" :label-width="80">
        <FormItem label="" prop="num">
          <Input v-model="formItem3.num" placeholder=""></Input>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script type="es6">
  export default {
    name: 'UserManage',
    data () {
      return {
        total: 0,
        condi: '',
        modal1: false,
        modal2: false,
        content:'',
        currIndex: 0,//
        formInline: {
          title: ''
        },
        formItem2: {
          title: '',
          author: '',
          publisher: '',
          publishtime: '',
          descri: ''
        },
        ruleItem2: {
          title: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          author: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          publisher: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          publishtime: [{
            required: true,
            message: ''
          }],
          descri: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        formItem3: {
          num: ''
        },
        ruleItem3: {
          num: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        columns7: [
          {
            title: '',
            key: 'aid',
            render: (h, params) => {
              return h('div', [
                h('Icon', {
                  props: {
                    type: 'document-text'
                  }
                }),
                h('strong', params.row.aid)
              ]);
            }
          },
          {
            title: '',
            key: 'title'
          },
          {
            title: '',
            key: 'author'
          },
          {
            title: '',
            key: 'publisher'
          },
          {
            title: '',
            key: 'publishtime'
          },
          {
            title: '',
            key: 'action',
            width: 300,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.modal2=true
                      this.currIndex = this.data6[params.index].aid
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          }
        ],
        data6: [
          {
            aid: "3044413",
            title:"xxx",
            author:"xxx",
            publisher:"xxxx",
            publishtime:"2017"
          }
    ],
        data7: []
      }
    },
    mounted(){
      this.request(1);
    },
    methods: {
      handleSubmit(account) {
        this.request(1)
      },
      show (index) {
        this.$Modal.info({
          title: '',
          width: '1100',
          //content: `${this.data6[index].title}<br>${this.data6[index].author}<br>${this.data6[index].publisher}<br>${this.data6[index].publishtime}<br>${this.data6[index].num}<br>${this.data6[index].descri}`
          content: `${this.data6[index].title}<br>${this.data6[index].author}<br>${this.data6[index].publisher}<br>${this.data6[index].publishtime}<br>${this.data6[index].num}<br>${this.data6[index].count}<br><span style="color:red;">${this.data6[index].suba}</span><br>${this.data6[index].descri}`
        })
      },
      remove (index) {
        this.data6.splice(index, 1);
      },
      request (currentPage){
        var that=this
        this.$http.post(that.GLOBAL.serverPath + '/excise/getAllAlbums',
          {
            title: that.formInline.title,
            currentPage: currentPage
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          console.log(res.data.pageInfo)
          that.total=res.data.pageInfo.total
          that.data6=[]
          that.data7=res.data.albums
          that.data7.forEach((e) => {
            let obj={}
            obj.aid = e.aid
            obj.title = e.title
            obj.author = e.author
            obj.publisher = e.publisher
            obj.publishtime = e.publishtime
            obj.num = e.num
            obj.descri = e.descri
            var count=0
            var s=''
            e.subalbums.forEach((item)=>{
              if( item.condi==0 ){
                count++
                s=s+item.number+','
              }
            })
            obj.count = count
            obj.suba = s
            that.data6.push(obj)
          })
        })
      },
      changePage: function(page){
        this.request(page)
      },
      ok (name) {
        var that=this
        this.$refs[name].validate((valid) => {
          if (valid) {
            that.$http.post(that.GLOBAL.serverPath + '/excise/addAlbum',
              {
                title: that.formItem2.title,
                author: that.formItem2.author,
                publisher: that.formItem2.publisher,
                publishtime: that.formItem2.publishtime+'',
                descri: that.formItem2.descri
              },
              {
                emulateJSON: true
              }
            ).then(function (res) {
              console.log(res.data.status)
              if(res.data.status=='ok'){
                //that.$Message.success('')
                that.$Notice.config({
                  top: 50,
                  duration: 3,
                  title: '',
                  desc: '!'
                })
                that.formInline.account=''
                that.formItem2.title=''
                that.formItem2.author=''
                that.formItem2.publisher=''
                that.formItem2.publishtime=''
                that.formItem2.descri=''
                that.request(1)
              }

            }).catch((e) => {
              that.$Message.fail('')
            })
          }
        })
      },
      ok2 (name) {
        var that=this
        this.$refs[name].validate((valid) => {
          if (valid) {
            that.$http.post(that.GLOBAL.serverPath + '/excise/addSubAlbum',
              {
                aid: that.currIndex,
                number: that.formItem3.num
              },
              {
                emulateJSON: true
              }
            ).then(function (res) {
              console.log(res.data.status)
              if(res.data.status=='ok'){
                that.$Message.success('')
                that.formItem3.num=''
                that.request(1)
              }else{
                that.$Message.error('')
              }

            }).catch((e) => {
              that.$Message.fail('')
            })
          }
        })
      },
      onEditorChange({editor,html,text}){
        // 
        console.log(editor,html,text)
        this.content = html
      }
    }
  }
</script>




