<template>
  <div class="container">
    <Form ref="formInline" :model="formInline" inline>
      <FormItem prop="account">
        <Input type="text" v-model="formInline.title" placeholder="">
        <Icon type="ios-search" slot="prepend"></Icon>
        </Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formInline')"></Button>
      </FormItem>
    </Form>
    <Table border :columns="columns7" :data="data6"></Table>
    <Page :total="total" :page-size="10" @on-change="changePage"></Page>
  </div>
</template>
<script type="es6">
  export default {
    name: 'SearchBook',
    data () {
      return {
        //
        total: '',
        condi: '',
        modal1: false,
        modal2: false,
        currIndex: 0,//
        formInline: {
          title: ''
        },
        formItem2: {
          title: '',
          author: '',
          publisher: '',
          publishtime: '',
          descri: ''
        },
        ruleItem2: {
          title: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          author: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          publisher: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          publishtime: [{
            required: true,
            message: ''
          }],
          descri: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        formItem3: {
          num: ''
        },
        ruleItem3: {
          num: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        //
        columns7: [
          {
            title: '',
            key: 'aid',
            //,
            render: (h, params) => {
              return h('div', [
                h('Icon', {
                  props: {
                    type: 'document-text'
                  }
                }),
                //:
                h('strong', params.row.aid)
              ]);
            }
          },
          {
            title: '',
            key: 'title'
          },
          {
            title: '',
            key: 'author'
          },
          {
            title: '',
            key: 'publisher'
          },
          {
            title: '',
            key: 'publishtime'
          },
          {
            title: '',
            key: 'count'
          },
          {
            title: '',
            key: 'action',
            width: 150,
            align: 'center',
            //,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      //show()
                      this.show(params.index)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small',
                    //
                    disabled: this.data6[params.index].unBorrowed
                  },
                  on: {
                    click: () => {
                      //borrow()
                      this.borrow(params.index)
                    }
                  }
                },'')
              ]);
            }
          }
        ],
        //
        data6: [
          {
            aid:'123456',
            title:'123456',
            author:'author',
            publisher:'publishtime',
            publishtime:'2017.10.1',
            count:'5',
            action:''
          }
        ],
        data7: [] //
      }
    },
    //mounted:HTMLajax
    mounted(){
      this.request(1);
    },
    methods: {
      handleSubmit(account) {
        this.request(1)
      },
      show (index) {
        this.$Modal.info({
          title: '',
          width: '1100',
          content: `${this.data6[index].title}<br>${this.data6[index].author}<br>${this.data6[index].publisher}<br>${this.data6[index].publishtime}<br>${this.data6[index].num}<br>${this.data6[index].count}<br><span style="color:red;">${this.data6[index].suba}</span><br>${this.data6[index].descri}`
        })
      },
      borrow (index) {
        var that=this
        // $http.post(url + data + functioin)
        this.$http.post(that.GLOBAL.serverPath + '/excise/borrow',
          {
            aid: that.data6[index].aid,
            rid: window.localStorage.getItem('userId'),
            raccount: window.localStorage.getItem('account')
          },
          //Webapplication/jsonemulateJSON
          //application/x-www-form-urlencodedMIME typeHTML
          {
            emulateJSON: true
          }
        ).then(function (res) {
          if(res.data.status == 'ok'){
            this.$Message.success(',')
            that.data6[index].count=that.data6[index].count-1
            if(that.data6[index].count == 0){
              //0
              this.data6[index].unBorrowed = true
            }
          }else{
            this.$Message.error('!')
          }
        })
      },
      request (currentPage){
        var that=this
        this.$http.post(that.GLOBAL.serverPath + '/excise/getAllAlbums',
          {
            title: that.formInline.title,
            currentPage: currentPage
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          console.log(res.data.pageInfo)
          that.total=res.data.pageInfo.total
          that.data6=[]
          that.data7=res.data.albums
          //data7obj
          //data7eobj,obj pushdata6
          that.data7.forEach((e) => {
            let obj={}
            obj.aid = e.aid
            obj.title = e.title
            obj.author = e.author
            obj.publisher = e.publisher
            obj.publishtime = e.publishtime
            obj.num = e.num
            obj.descri = e.descri
            var count=0
            var s=''
            e.subalbums.forEach((item)=>{
              if( item.condi==0 ){
                count++
                s=s+item.number+','
              }
            })
            if(count == 0){
              obj.unBorrowed = true
            }else{
              obj.unBorrowed = false
            }
            obj.count = count
            obj.suba = s
            that.data6.push(obj)
          })
        })
      },
      changePage: function(page){
        this.request(page)
      }
    }
  }
</script>




