<template>
  <div class="container">
    <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
      <FormItem prop="account">
        <Input type="text" v-model="formInline.account" placeholder="">
        <Icon type="ios-person-outline" slot="prepend"></Icon>
        </Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formInline')"></Button>
      </FormItem>
    </Form>
    <Table border :columns="columns7" :data="data6"></Table>
    <Page :total="total" :page-size="10" @on-change="changePage"></Page>
  </div>
</template>
<script type="es6">
  export default {
    name: 'Feedback',
    data () {
      return {
        total: '',
        condi: '',
        formInline: {
          account: ''
        },
        columns7: [
          {
            title: '',
            key: 'account',
            //,
            render: (h, params) => {
              return h('div', [
                h('Icon', {
                  props: {
                    type: 'person'
                  }
                }),
                //
                h('strong', params.row.account)
              ]);
            }
          },
          {
            title: '',
            key: 'name'
          },
          {
            title: '',
            key: 'sex'
          },
          {
            title: '',
            key: 'condi'
          },
          {
            title: '',
            key: 'email'
          },
          {
            title: '',
            key:'feedback'
          },
          {
            title: '',
            key: 'action',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                //,
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                        //remove()
                        this.remove(params.index);
                      this.$Message.info('',3);
                    }
                  }
                }, '')
              ]);
            }
          }
        ],
        data6: [
          {
            account:'201830681053',
            name:'',
            sex:'',
            condi:'',
            email:'123456789@qq.com',
            feedback:''
          }
        ]
      }
    },
    //mounted:HTMLajax
    mounted(){
      this.request(1);
    },
    methods: {
      handleSubmit(account) {
        this.request(1)
      },
      show (index) {
        if(this.data6[index].condi==0){
          this.condi=''
        }else{
          this.condi=''
        }
        this.$Modal.info({
          title: '',
          content: `${this.data6[index].account}<br>${this.data6[index].name}<br>${this.data6[index].sex}<br>${this.condi}<br>${this.data6[index].email}<br>${this.data6[index].feedback}`
        })
      },
      remove (index) {
        //
        this.data6.splice(index, 1);
      },
      request (currentPage){
        var that=this
        // $http.post(url + data + functioin)
        this.$http.post(that.GLOBAL.serverPath + '/excise/getAllReaders',
          {
            account: that.formInline.account,
            currentPage: currentPage
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          console.log(res.data.pageInfo)
          that.total=res.data.pageInfo.total
          that.data6=res.data.readers
        }).catch((e) => {
          this.$Message.fail('')
        })
      },
      changePage: function(page){
        this.request(page)
      }
    }
  }
</script>




