<template>
  <div class="container">
    <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
      <FormItem prop="borrowRecord">
        <Input type="text" v-model="formInline.raccount" placeholder="">
        <Icon type="ios-person-outline" slot="prepend"></Icon>
        </Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formInline')"></Button>
      </FormItem>
    </Form>
    <Table border :columns="columns7" :data="data6"></Table>
    <Page :total="total" :page-size="10" @on-change="changePage"></Page>
  </div>
</template>
<script type="es6">
  export default {
    name: 'UserManage',
    data () {
      return {
        total: '',
        condi: '',
        modal1: false,
        modal2: false,
        currIndex: 0,//
        formInline: {
          raccount: ''
        },
        columns7: [
          {
            title: '',
            key: 'bid',
            //,
            render: (h, params) => {
              return h('div', [
                h('Icon', {
                  props: {
                    type: 'document-text'
                  }
                }),
                //:
                h('strong', params.row.bid)
              ]);
            }
          },
          {
            title: '',
            key: 'title'
          },
          {
            title: '',
            key: 'number'
          },
          {
            title: '',
            key: 'raccount'
          },
          {
            title: '',
            key: 'rname'
          },
          {
            title: '',
            key: 'time'
          },
          {
            title: '',
            key: 'backtime'
          },
          {
            title: '',
            key: 'condi'
          },
          {
            title: '',
            key: 'action',
            width: 150,
            align: 'center',
            //,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    //show()
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    //show()
                    click: () => {
                      this.reback(params.index)
                    }
                  }
                }, '')
              ]);
            }
          }
        ],
        data6: [],
        data7: [] //
      }
    },
    //mounted:HTMLajax
    mounted(){
      this.request(1);
    },
    methods: {
      handleSubmit(account) {
        this.request(1)
      },
      show (index) {
        this.$Modal.info({
          title: '',
          content: `${this.data6[index].title}<br>${this.data6[index].number}<br>${this.data6[index].raccount}<br>${this.data6[index].rname}<br>${this.data6[index].time}<br>${this.data6[index].backtime}<br>${this.data6[index].condi}`
        })
      },
      reback (index) {
        //this.data6.splice(index, 1);
        var that=this
        this.$http.post(that.GLOBAL.serverPath + '/excise/reback',
          {
            bid: that.data6[index].bid,
            sid: that.data6[index].sid
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          if(res.data.status == 'yes'){
            that.data6.splice(index,1)
            this.$Message.success('')
          }else{
            this.$Message.status('')
          }
        })
      },
      request (currentPage){ //
        var that=this
        this.$http.post(that.GLOBAL.serverPath + '/excise/getAllBorrowRecords',
          {
            raccount: that.formInline.raccount,
            currentPage: currentPage
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          console.log(res.data.borrowrecords)
          that.total=res.data.pageInfo.total
          that.data6=[]
          that.data7=res.data.borrowrecords
          that.data7.forEach((e) =>{
            //data7obj
            //data7eobj,obj pushdata6
            let obj = {}
            obj.bid = e.bid
            obj.title = e.album.title
            obj.number = e.subalbum.number
            obj.raccount = e.raccount
            obj.rname = e.reader.name
            obj.time = e.time
            obj.backtime = e.backtime
            obj.sid = e.sid
            var time = new Date().getTime();
            console.log(time)
            if(time < e.inttime){
              obj.condi = ''
            }else{
              var delayDay = Math.ceil((time - e.inttime)/86400000)
              obj.condi = ''+delayDay+''
            }
            that.data6.push(obj)
          })
        })
      },
      changePage: function(page){
        this.request(page)
      },
    }
  }
</script>




