<template>
  <div class="container">
    <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
      <FormItem prop="account">
        <Input type="text" v-model="formInline.account" placeholder="">
        <Icon type="ios-person-outline" slot="prepend"></Icon>
        </Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formInline')"></Button>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="modal1 = true"></Button>
      </FormItem>
    </Form>
    <Table border :columns="columns7" :data="data6"></Table>
    <Page :total="total" :page-size="10" @on-change="changePage"></Page>

    <Modal
      v-model="modal1"
      title=""
      @on-ok="ok('formItem2')"
    >
      <Form ref="formItem2" :model="formItem2" :rules="ruleItem2" :label-width="80">
        <FormItem label="" prop="account">
          <Input v-model="formItem2.account" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="name">
          <Input v-model="formItem2.name" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="sex">
          <RadioGroup v-model="formItem2.sex">
            <Radio label=""></Radio>
            <Radio label=""></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="" prop="condi">
          <Select v-model="formItem2.condi">
            <Option value="0"></Option>
            <Option value="1"></Option>
          </Select>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script type="es6">
  export default {
    name: 'UserManage',
    data () {
      return {
        total: '',
        condi: '',
        modal1: false,
        formInline: {
          account: ''
        },
        formItem2: {
          account: '',
          name: '',
          sex: '',
          condi: '0'
        },
        ruleItem2: {
          account: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        columns7: [
          {
            title: '',
            key: 'account',
            //,
            render: (h, params) => {
              return h('div', [
                h('Icon', {
                  props: {
                    type: 'person'
                  }
                }),
                //
                h('strong', params.row.account)
              ]);
            }
          },
          {
            title: '',
            key: 'name'
          },
          {
            title: '',
            key: 'sex'
          },
          {
            title: '',
            key: 'time'
          },
          {
            title: '',
            key: 'condi'
          },
          {
            title: '',
            key: 'action',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                //,
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      //show()
                      this.show(params.index)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      //remove()
                      this.remove(params.index)
                    }
                  }
                }, '')
              ]);
            }
          }
        ],
        data6: []
      }
    },
    //mounted:HTMLajax
    mounted(){
      this.request(1);
    },
    methods: {
      handleSubmit(account) {
        this.request(1)
      },
      show (index) {
        if(this.data6[index].condi==0){
          this.condi=''
        }else{
          this.condi=''
        }
        this.$Modal.info({
          title: '',
          content: `${this.data6[index].name}<br>${this.data6[index].sex}<br>${this.data6[index].account}<br>${this.condi}`
        })
      },
      remove (index) {
        //
        this.data6.splice(index, 1);
      },
      request (currentPage){
        var that=this
        // $http.post(url + data + functioin)
        this.$http.post(that.GLOBAL.serverPath + '/excise/getAllReaders',
          {
            account: that.formInline.account,
            currentPage: currentPage
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          console.log(res.data.pageInfo)
          that.total=res.data.pageInfo.total
          that.data6=res.data.readers
        }).catch((e) => {
          this.$Message.fail('')
        })
      },
      changePage: function(page){
        this.request(page)
      },
      //
      ok (name) {
        var that=this
        this.$refs[name].validate((valid) => {
          if (valid) {
            that.$http.post(that.GLOBAL.serverPath + '/excise/addReader',
              {
                account: that.formItem2.account,
                name: that.formItem2.name,
                sex: that.formItem2.sex,
                condi: that.formItem2.condi,
              },
              {
                emulateJSON: true
              }
            ).then(function (res) {
              console.log(res.data.status)
              if(res.data.status=='ok'){
                that.$Message.success('')
                that.formInline.account=''
                that.request(1)
              }else{
                that.$Message.error('')
              }

            }).catch((e) => {
              that.$Message.fail('')
            })
          }
        })
      }
    }
  }
</script>




