<template>
  <div class="layout">
    <Modal v-model="modal6" width="360">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span></span>
      </p>
      <div style="text-align:center">
        <p></p>
        <p></p>
      </div>
      <div slot="footer">
        <i-button type="error" size="large" style="color:white" long :loading="modal_loading" @click="del()"></i-button>
      </div>
    </Modal>

    <Modal
      v-model="modal2"
      title=""
      @on-ok="ok"
      @on-cancel="cancel">
      <p><Rate show-text :value.sync="value"></Rate></p>
      <i-input type="textarea" :rows="4" placeholder="..."></i-input>
    </Modal>

    <Modal v-model="modal7" width="600">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span></span>
      </p>
      <div>
        <p></p>
        <p> &nbsp;&nbsp;&nbsp;&nbsp;</p>
        <br>
        <h4></h4>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;1. xx</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;2. </p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;3. 560</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;4. 0.1</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;5. </p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;6. </p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;7. </p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;8. </p>
        <br>
        <h4></h4>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;1. 1-22</p>
        <p>&nbsp;&nbsp;&nbsp;&nbsp;2. 2</p>
        <br>
        <p></p>
        <br>
        <div style="text-align:right">
          <p>xx</p>
        </div>
      </div>
      <div slot="footer">
        <i-button type="error" size="large" style="color:white" long :loading="modal_loading" @click="confirm()"></i-button>
      </div>
    </Modal>

    <div class="layout-ceiling">
      <div class="layout-ceiling-left">
        <div class="layout-logo-left">
          <h2 style="color:white;"></h2>
        </div>
      </div>
      <div class="layout-ceiling-main">
        <a href="#"> <Icon type="ios-browsers-outline"></Icon></a>
        <a @click="modal7 = true"></a> |
        <a @click="modal2 = true"></a> |
        <a  @click="modal6 = true"></a>
      </div>
    </div>
    <div class="layout-header">
      <div class="layout-logo"></div>
    </div>
    <div class="layout-content">
    <Row type="flex">
      <Col span="5" class="layout-menu-left">
      <Menu active-name="1-1" theme="dark" width="auto" :open-names="['1']">
        <Submenu name="1">
          <template slot="title">
            <Icon type="ios-navigate"></Icon>
            {{msg}}
          </template>
          <MenuItem name="1-1" @click.native="searchBook"><span>
            <Icon type="ios-search"></Icon>
            </span></MenuItem>
          <MenuItem name="1-2" @click.native="lookRecord"><span>
            <Icon type="ios-bookmarks"></Icon>
            </span></MenuItem>
          <MenuItem name="1-3" @click.native="individualInfo"><span>
            <Icon type="ios-person"></Icon>
            </span></MenuItem>
        </Submenu>
        <!--<Submenu name="2">
          <template slot="title">
            <Icon type="ios-keypad"></Icon>
            
          </template>
          <MenuItem name="2-1"><span @click="lookBook"></span></MenuItem>
          <MenuItem name="2-2">Option 2</MenuItem>
        </Submenu>
        <Submenu name="3">
          <template slot="title">
            <Icon type="ios-analytics"></Icon>
            
          </template>
          <MenuItem name="3-1">Option 1</MenuItem>
          <MenuItem name="3-2">Option 2</MenuItem>
        </Submenu>-->
      </Menu>
      </Col>
      <Col span="19">
      <div class="layout-breadcrumb">
        <Breadcrumb>
          <BreadcrumbItem href="#">{{one_nav}}</BreadcrumbItem>
          <BreadcrumbItem href="#">{{two_nav}}</BreadcrumbItem>
          <BreadcrumbItem>{{three_nav}}</BreadcrumbItem>
          <Button id="login_btn"  @click="warning(false)">
            <Badge dot><Icon type="ios-mail-outline"size="26" color:white></Icon></Badge></Button>
        </Breadcrumb>
      </div>
      <div class="layout-content">
        <div class="layout-content-main">
          <template id="searchBook"></template>
          <template id="myRecord"></template>
          <template id="individual"></template>
          <component :is="currentView"></component>
        </div>
      </div>
      <div style="height: 600px"></div>
      <div class="layout-copy">
        2019-2020 &copy; 
      </div>
      </Col>
    </Row>
  </div>
  </div>
</template>
<script>
  import searchBook from '../components/SearchBook.vue'
  import myRecord from '../components/MyRecord.vue'
  import individual from '../components/Individual.vue'
  import Button from 'iview/src/components/button/button'
  export default {
    name: 'Reader',
    data () {
      return {
        msg: '',
        one_nav: '',
        two_nav: '',
        three_nav: '',
        currentView: 'searchBook',
        modal7: false,
        modal2: false,
        modal6: false,
        modal_loading: false,
        value:5.0
      }
    },
    mounted () {
      this.msg = window.localStorage.getItem('username')
    },
    methods: {
      searchBook() {
        this.one_nav = ''
        this.two_nav = ''
        this.three_nav = ''
        this.currentView = 'searchBook'
      },
      lookRecord() {
        this.one_nav = ''
        this.two_nav = ''
        this.three_nav = ''
        this.currentView = 'myRecord'
      },
      individualInfo() {
        this.one_nav = ''
        this.two_nav = ''
        this.three_nav = ''
        this.currentView = 'individual'
      },
      returnTime(currentPage) {
        var that = this
        this.$http.post(that.GLOBAL.serverPath + '/excise/getAllBorrowRecords',
          {
            raccount: window.localStorage.getItem('account'),
            currentPage: currentPage
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          console.log(res.data.borrowrecords)
          that.total = res.data.pageInfo.total
          that.data7 = res.data.borrowrecords
          that.books = []
          that.data7.forEach((e) => {
            let obj = {}
            obj.title = "" + e.album.title + ""
            var time = new Date().getTime();
            console.log(time)
            var delayDay = Math.ceil((time - e.inttime) / 86400000)
            if (delayDay == 1) {
              that.books.push(obj);
            }
          })
        })
      },
      warning(nodesc) {
        this.$Notice.warning({
          title: '',
          desc: nodesc ? '' : '' + this.returnTime(1).books + '1',
        });
      },
      del() {
        this.modal_loading = true;
        setTimeout(() => {
          this.modal_loading = false;
          this.modal6 = false;
          this.$Message.success('');
          this.$Loading.start();
          this.$Loading.finish();
          this.$router.push('/login');
        }, 2000);
      },
      confirm() {
        this.modal_loading = true;
        this.modal_loading = false;
        this.modal7 = false;
      },
      ok() {
        this.$Message.info('');
      },
      cancel() {
        this.$Message.info('');
      },
      changePage1() {
        this.$Loading.start();
        this.$Loading.finish();
        this.$router.push('/login')
      }
      },
      components: {
        Button,
        searchBook: searchBook,
        myRecord: myRecord,
        individual: individual
      }
    }
</script>
<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    margin-top:-60px;
  }
  .layout-breadcrumb{
    padding: 10px 15px 0;
    text-align:left;
  }
  .layout-ceiling{
    background: #464c5b;
    padding: 55px 0;
    overflow: hidden;
  }
  .layout-ceiling-main{
    float: right;
    margin-right: 15px;
    margin-top: 25px;
    margin-bottom:-90px;
  }
  .layout-ceiling-left{
    float: left;
    margin-right: 15px;
    margin-top: 5px;
    margin-bottom:-80px;
    width:200px;
  }
  .layout-ceiling-main a{
    color: #9ba7b5;
  }
  .layout-content{
    min-height: 200px;
    margin: 15px;
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
  }
  .layout-content-main{
    padding: 10px;
  }
  .layout-copy{
    text-align: center;
    padding: 10px 0 20px;
    color: #9ea7b4;
  }
  .layout-menu-left{
    background: #464c5b;
  }
  .layout-header{
    height: 10px;
    background: #fff;
    box-shadow: 0 1px 1px rgba(0,0,0,.1);
  }
  .layout-logo-left{
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
  }
  #login_btn{
    width:10%;
    margin-left:795px;
  }
  Button{
    color: #0553ff;
  }
</style>



