<template>
  <div class="container">
    <Form ref="formInline" :model="formInline" :rules="ruleInline" inline>
      <FormItem prop="account">
        <Input type="text" v-model="formInline.account" placeholder="">
        <Icon type="ios-person-outline" slot="prepend"></Icon>
        </Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formInline')"></Button>
      </FormItem>
    </Form>
    <Table border :columns="columns7" :data="data6"></Table>
    <Page :total="total" :page-size="10" @on-change="changePage"></Page>

    <Modal
      v-model="modal1"
      title=""
      @on-ok="remove(params.index)">
      <div style="text-align:center">
        <p></p>
      </div>
    </Modal>

    <Modal
      v-model="modal2"
      title=""
      @on-ok="ok(params.index)">
      <div style="text-align:center">
        <p></p>
      </div>
    </Modal>

  </div>
</template>

<script type="es6">
  export default {
    name: 'VerifyUser',
    data () {
      return {
        //
        total: '',
        condi: '',
        modal1: false,
        formInline: {
          account: ''
        },
        formItem2: {
          account: '',
          name: '',
          sex: '',
          condi: '0',
          number:'',
          email:'',
        },
        ruleItem2: {
          account: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        columns7: [
          {
            title: '',
            key: 'account',
            render: (h, params) => {
              return h('div', [
                h('Icon', {
                  props: {
                    type: 'person'
                  }
                }),
                h('strong', params.row.account)
              ]);
            }
          },
          {
            title: '',
            key: 'name'
          },
          {
            title: '',
            key: 'sex'
          },

          {
            title: '',
            key: 'condi'
          },

          {
            title: '',
            key: 'action',
            width: 300,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                //
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, ''),

                //
                h('Button', {
                  props: {
                    type: 'success',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                      //
                      this.showSucess()
                    }
                  }
                }, ''),

                //
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      //
                      this.modal1=true
                      //
                      this.remove(params.index)
                    }
                  }
                }, '')

              ]);
            }
          }
        ],

        //
        data6: [
          {
            account:'123456',
            name:'',
            sex:'',
            condi:'',
            number:'123456789',
            email:'123456@qq.com',

          },
          {
            account:'123456',
            name:'',
            sex:'',
            condi:'',
            number:'123456789',
            email:'123456@qq.com',

          }
        ]
      }
    },
    mounted(){
      this.request(1);
    },
    methods: {
      handleSubmit(account) {
        this.request(1)
      },

      //
      show (index) {
        this.$Modal.info({
          title: '',
          content: `${this.data6[index].name}
          <br>${this.data6[index].sex}
          <br>${this.data6[index].account}
          <br>${this.data6[index].condi}
          <br>: ${this.data6[index].number}
          <br>${this.data6[index].email}`

        })
      },
      showSucess(){
        this.$Modal.info({
          title: '',
          content: ``
        })
      },


      //
      remove (index) {
        this.data6.splice(index, 1);
      },

      //
      request (currentPage){
        var that=this
        this.$http.post(that.GLOBAL.serverPath + '/excise/getAllReaders',
          {
            account: that.formInline.account,
            currentPage: currentPage
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          console.log(res.data.pageInfo)
          that.total=res.data.pageInfo.total
          that.data6=res.data.readers
        }).catch((e) => {
          this.$Message.fail('')
        })
      },
      changePage: function(page){
        this.request(page)
      },

      //
      ok (name) {
        var that=this
        this.$refs[name].validate((valid) => {
          if (valid) {
            that.$http.post(that.GLOBAL.serverPath + '/excise/addReader',
              {
                account: that.formItem.account,
                name: that.formItem.name,
                sex: that.formItem.sex,
                condi: that.formItem.condi,
              },
              {
                emulateJSON: true
              }
            ).then(function (res) {
              console.log(res.data.status)
              if(res.data.status=='ok'){
                that.$Message.success('')
                that.formInline.account=''
                that.request(1)
              }else{
                that.$Message.error('')
              }

            }).catch((e) => {
              that.$Message.fail('')
            })
          }
        })
      }
    }
  }
</script>




