<template>
  <div class="container">
    <Table border :columns="columns7" :data="data6"></Table>
    <Page :total="total" :page-size="10" @on-change="changePage"></Page>
  </div>
</template>
<script type="es6">
  export default {
    name: 'MyRecord',
    data () {
      return {
        total: '',
        condi: '',
        modal1: false,
        modal2: false,
        currIndex: 0,//
        formInline: {
          raccount: ''
        },
        //
        columns7: [
          {
            title: '',
            key: 'bid',
            //,
            render: (h, params) => {
              return h('div', [
                h('Icon', {
                  props: {
                    type: 'document-text'
                  }
                }),
                //:
                h('strong', params.row.bid)
              ]);
            }
          },
          //
          {
            title: '',
            key: 'title'
          },
          {
            title: '',
            key: 'number'
          },
          {
            title: '',
            key: 'raccount'
          },
          {
            title: '',
            key: 'rname'
          },
          {
            title: '',
            key: 'time'
          },
          {
            title: '',
            key: 'backtime'
          },
          {
            title: '',
            key: 'condi'
          }
        ],
        data6: [],
        data7: [] //
      }
    },
    //mounted:HTMLajax
    mounted(){
      this.request(1);
    },
    methods: {
      request (currentPage){ //
        var that=this
        // $http.post(url + data + functioin)
        this.$http.post(that.GLOBAL.serverPath + '/excise/getAllBorrowRecords',
          {
            raccount: window.localStorage.getItem('account'),
            currentPage: currentPage
          },
          {
            emulateJSON: true
          }
        ).then(function (res) {
          console.log(res.data.borrowrecords)
          that.total=res.data.pageInfo.total
          that.data6=[]
          that.data7=res.data.borrowrecords
          //data7obj
          //data7eobj,obj pushdata6
          that.data7.forEach((e) =>{
            let obj = {}
            obj.bid = e.bid
            obj.title = e.album.title
            obj.number = e.subalbum.number
            obj.raccount = e.raccount
            obj.rname = e.reader.name
            obj.time = e.time
            obj.backtime = e.backtime
            var time = new Date().getTime();
            console.log(time)
            if(time < e.inttime){
              obj.condi = ''
            }else{
              var delayDay = Math.ceil((time - e.inttime)/86400000)
              obj.condi = ''+delayDay+''
            }
            that.data6.push(obj)
          })
        })
      },
      changePage: function(page){
        this.request(page)
      },
    }
  }
</script>




